@import "../utils/mixins";
@import "../utils/vars";

@font-face {
  font-family: Mont-Regular;
  src: url("../fonts/Mont-Regular.otf");
}

@font-face {
  font-family: Mont-Bold;
  src: url("../fonts/Mont-Bold.otf");
}

@font-face {
  font-family: Mont-SemiBold;
  src: url("../fonts/Mont-SemiBold.otf");
}

%h1 {
  font-family: Mont-Bold, sans-serif;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: 0.01em;

  @include on-tablet {
    font-size: 22px;
    line-height: 31px;
  }
}

%h2 {
  font-family: Mont-Bold, sans-serif;
  font-size: 22px;
  line-height: 31px;

  @include on-tablet {
    font-size: 18px;
    line-height: 27px;
  }
}

%h3 {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 20px;
  line-height: 26px;

  @include on-tablet {
    font-size: 16px;
    line-height: 22px;
  }
}

%navigation {
  font-family: Mont-Bold, sans-serif;
  font-size: 12px;
  line-height: 11px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $secondary;

  &:hover {
    color: $white;
  }
}

%body {
  font-family: Mont-Regular, sans-serif;
  font-size: 14px;
  line-height: 21px;

  @include on-tablet {
    font-size: 13px;
    line-height: 18px;
  }
}

%button {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: $white;

  @include on-tablet {
    font-size: 12px;
    line-height: 18px;
  }
}

%small-text {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 12px;
  line-height: 15px;

  @include on-tablet {
    font-size: 12px;
    line-height: 13px;
  }
}

@font-face {
  font-family: Mont-Bold;
  src: url(../fonts/Mont-Bold.otf);
  font-weight: 700;
}

@font-face {
  font-family: Mont-Regular;
  src: url(../fonts/Mont-Regular.otf);
  font-weight: 500;
}

@font-face {
  font-family: Mont-SemiBold;
  src: url(../fonts/Mont-SemiBold.otf);
  font-weight: 600;
}

%mont-700 {
  font-family: Mont-Bold, sans-serif;
  font-weight: 700;
}

%mont-600 {
  font-family: Mont-SemiBold, sans-serif;
  font-weight: 600;
}

%mont-500 {
  font-family: Mont-Regular, sans-serif;
  font-weight: 500;
}
